import { CurriculumService } from './curriculum.service';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { PluginService } from '../shared/plugin/plugin.service';
import { JhiEventManager, JhiParseLinks } from 'ng-jhipster';
import { Principal } from '../core';
import { CoreErrorHandler } from '../shared/error-handler/error-handler.service';
import { HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { IOrganisationSubSchool } from '../shared/model/common/organisation-sub-school.model';
import { Subscription } from 'rxjs';
import { ITEMS_PER_PAGE } from '../shared';

declare var $: any;
declare var swal: any;

@Component({
    templateUrl: "./curriculum-affiliations.component.html",
    providers: [CurriculumService]
})
export class CurriculumAffiliationsComponent implements OnInit {
    affiliations: IOrganisationSubSchool[];
    eventSubscriber: Subscription;
    itemsPerPage: number;
    totalItems: number;
    totalPages: number;
    currentAccount: any;
    predicate: any;
    reverse: any;
    links: any;
    page: any;

    constructor(
        public _router: Router,
        public curriculumService: CurriculumService,
        protected eventManager: JhiEventManager,
        protected parseLinks: JhiParseLinks,
        protected principalService: Principal,
        protected pluginService: PluginService,
        public errorHandler: CoreErrorHandler,
        public activatedRoute: ActivatedRoute
    ) {
        this.affiliations = [];
        this.itemsPerPage = ITEMS_PER_PAGE;
        this.page = 0;
        this.links = {
            last: 0
        };
        this.predicate = 'id';
        this.reverse = false;
    }

    ngOnInit() {
        this.pluginService.sideMenuToggle();
        let curriculumId;
        curriculumId = this.activatedRoute.snapshot.params["id"];
        if (curriculumId) {
            this.loadCurriculumAffiliations(curriculumId);
        }
        this.principalService.identity().then(account => {
            this.currentAccount = account;
        });
    }

    loadCurriculumAffiliations(curriculumId) {
        this.curriculumService
            .getAffiliationsByCurriculumId(curriculumId)
            .subscribe(
                (res: HttpResponse<IOrganisationSubSchool[]>) => this.paginateCurriculumAffiliations(res.body, res.headers),
                (res: HttpErrorResponse) => this.onError(res)
            );
    }

    protected paginateCurriculumAffiliations(data: IOrganisationSubSchool[], headers: HttpHeaders) {
        //this.links = this.parseLinks.parse(headers.get('link'));
        this.totalItems = parseInt(headers.get('X-Total-Count'), 10);
        if (data.length > 0) {
            for (let i = 0; i < data.length; i++) {
                this.affiliations.push(data[i]);
            }
        } else {
            this.pluginService.alertMessage("No data found..!", "warning");
        }
    }

    protected onError(errRes: HttpErrorResponse) {
        this.errorHandler.handleError(errRes);
    }

    public onBack() {
        var localObj = this;
        swal({
            title: "Are you sure to go previous page ?",
            type: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes",
            confirmButtonColor: "#32b432",
            cancelButtonText: "No",
            closeOnConfirm: true,
            closeOnCancel: true
        },
            function (isConfirm) {
                if (isConfirm) {
                    localObj.previousUrl();
                    localObj.pluginService.sideMenuToggle();
                }
            });
    }

    previousUrl() {
        this._router.navigateByUrl('/curriculum');
    }
}